import React, { useMemo } from "react";
// import PropTypes from "prop-types";
import Footer from "../../component/Footer";
import Button from "../../component/Button";
import styled from "styled-components";
import FlexBox from "../../component/FlexBox";
import { useHistory } from "react-router-dom";
import Bg from "../../images/BG.jpg";
import ScrollHint from "./ScrollHint";
import MainInfos from "./mainInfo/MainInfos";
import MoreInfoBanner from "./mainInfo/MoreInfoBanner";
import useRWD, { useRWDStyleParams } from "../../hooks/useRWD";
import PageContainer from "../../component/PageContainer";
import screenEnum from "../../constant/screenEnum";

const Container = styled(PageContainer)`
  width: 100%;
  overflow: visible;
`;

const FullHeightContent = styled(({ paddingLeft, isSmScreen, ...rest }) => (
  <FlexBox {...rest} />
))`
  background-image: url("${Bg}");
  background-position: left;
  padding-left: ${({ paddingLeft }) => paddingLeft};
  color: #fff;
  font-size: ${({ isSmScreen }) => (isSmScreen ? "16px" : "20px")};
  height: 100%;
  position: relative;

  & > *:not(:last-child) {
    margin-bottom: 16px;
  }
`;

const StyledButton = styled(Button)`
  max-width: 288px;
  height: 60px;
  font-size: 20px;
  margin-top: 20px;
`;

const Title = styled.span`
  font-weight: bold;
  font-size: ${({ isSmScreen }) => (isSmScreen ? "32px" : "40px")};
`;

function Home() {
  const history = useHistory();
  const { mainPadding } = useRWDStyleParams();
  const { screen } = useRWD();
  const isSmScreen = useMemo(() => screen <= screenEnum.sm, [screen]);

  return (
    <Container>
      <FullHeightContent
        isSmScreen={isSmScreen}
        justify="center"
        flex
        paddingLeft={mainPadding}
      >
        <Title isSmScreen={isSmScreen}></Title>
        <FlexBox>
          
          <br />  300 
          <br />  300  Youbike 
          <br /> 
        </FlexBox>
        <StyledButton onClick={() => history.push("/plan")}>
          
        </StyledButton>
        <ScrollHint />
      </FullHeightContent>
      <MainInfos />
      <MoreInfoBanner />

      <Footer />
    </Container>
  );
}

Home.propTypes = {};

export default Home;
